.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* 설화수 css */
*{margin: 0; padding: 0; box-sizing: border-box;}
a{
  text-decoration: none;
  color: inherit;
}
ul,ol,li{
  list-style: none;
}
img{
  max-width: 100%;
  vertical-align: top;
}
/* header */
#header{
  width: 100%;
  height: 122px;
}
.up{
  width: 1100px;
  height: 78px;
  margin: 0 auto;
  /* background: rgba(255,0,0,0.2); */
  /* display: flex;
  justify-content: space-between; */
  position: relative;
  z-index: 20;
}
.left{
  width: 200px;
  position: relative;
  float: left;
  font-size: 14px;
}
.lang{
  position: absolute;
  width: 100px;
  height: 35px;
  line-height: 35px;
  left: 0;
  top: 10px;
  z-index: 20;
  /* background: #ff0; */
}
.lang>a{
  color: #fff;
}
.l_list{
  position: absolute;
  width: 200px;
  background: #fff;
  padding: 10px;
  text-align: left;
  left: 0;
  top: 50px;
  z-index: 20;
  line-height: 26px;
  /* display: none; */
}
.logo{
  width: 220px;
  height: 42px;
  /* margin: 18px auto; */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  z-index: 20;
}
.logo>a{
  display: block;
  height: 42px;
  background: url(/public/images/logo_1.png) no-repeat left top/contain;
}
.util{
  float: right;
}
.util ul{
  display: flex;
  margin-top: 20px;
  font-size: 14px;
}
.util li{
  padding: 0 10px;
  border-right: 1px solid #ccc;
}
.util li a{
  color: #fff;
}
.util li:last-child{
  border-right: none;
}
.util li:last-child>a{
  display: block;
  width: 33px;
  height: 18px;
  background: url(/public/images/ser_0.png) no-repeat center/auto;
}
/* hover */
#header:hover{
  background: #fff;
  position: relative;
  z-index: 20;
}
#header:hover a{
  color: #666;
}
#header:hover .logo a{
  background-image: url(/public/images/logo_0.png);
}
#header:hover .util li:last-child a{
  background-image: url(/public/images/ser_1.png);
}
/* .down */
.down{
  height: 44px;
  text-align: center;position: relative;
  z-index: 20;
}
#nav>ul{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
}
#nav>ul>li{
  padding: 0 20px;
}
#nav>ul>li>a{
  display: block;
  height: 44px;
  line-height: 44px;
  color: #fff;
  position: relative;
}
#nav>ul>li>a:after{
  content: "";
  display: block;
  position: absolute;
  left: 0%;
  bottom: 1px;
  width: 0%;
  height: 3px;
  background: #f90;
  transition: all 0.5s;
}
#nav>ul>li>a:hover:after{
  width: 100%;
}
#nav .sub_back{
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  text-align: right;
  z-index: 99;
  padding: 10px 0;
  display: none;
  border-top: 1px solid #aaa;
}
#nav .sub_back .subIn{
  width: 1100px;
  margin: 0 auto;
}
#nav>ul>li:hover .sub_back{
  display: block;
}
#nav .sub_back .subIn>ul{
  width: 500px;
  float: right;
  text-align: left;
}
#nav .sub_back .subIn>ul>li{
  display: inline-block;
}
#nav>ul>li:nth-child(6) .sub_back .subIn>ul{
  margin-right: 70px;
}
#nav>ul>li:nth-child(7) .sub_back .subIn>ul{
  margin-right: 100px;
}
#nav>ul>li:nth-child(8) .sub_back .subIn>ul{
  margin-right: -20px;
}
#nav .sub>li>a{
  display: block;
  padding: 0 20px;
  color: #666;
}
#nav>ul>li:nth-child(9) .sub_back .subIn>ul{
  width: 100%;
  text-align: center;
}
#nav>ul>li:nth-child(9) .sub_back .subIn>ul>li{
  vertical-align: top;
  margin: 0 80px;
}
#nav>ul>li:nth-child(9) .sub_back .subIn>ul>li>a{
  color: #333;
  font-weight: 700;
  text-align: left;
  line-height: 30px;
}
#nav>ul>li:nth-child(9) .sub_back .subIn>ul ol{
  text-align: left;
  line-height: 26px;
}
/* main */
.main{
  height: 670px;
  /* background: rgba(255,0,0,0.2); */
  top: -122px;
  position: relative;
  /* border: 3px solid #f00; */
}
.main .txtBox{
  position: absolute;
  width: 1440px;
  height: 80px;
  bottom: 160px;
  color: #fff;
  left: 50%;
  transform: translateX(-50%);
  text-align: left;
}
.main .txtBox p:nth-child(1){
  font-size: 18px;
  height: 27px;
}
.main .txtBox p:nth-child(2){
  height: 53px;
  font-size: 36px;
}
.main .swiper-button-prev:after,
.main .swiper-button-next:after{
  color: #333;
}
.main .swiper-pagination-bullet{
  padding: 7px;
  margin: 10px;
  background: #333;
}
/* section */
.section{
  width: 1440px;
  height: auto;
  margin: 0 auto;
}
#section1{
  height: 706px;
  /* background: rgba(255,0,0,0.2); */
  margin-top: 120px;
}
#section1 h2{
  width: 1440px;
  height: 62px;
  font-size: 28px;
  padding-bottom: 20px;
  text-align: left;
  font-weight: 700;
}
#section1 .boxWrap{
  height: 644px;
}
#section1 .boxWrap .slide{
  width: 1440px;
  height: 581px;
  overflow: hidden;
}
#section1 .boxWrap .slide .imgBox{
  width: 464px;
  height: 464px;
  margin-bottom: 24px;
}
#section1 .boxWrap .slide .imgBox img{
  width: 100%;
}
#section1 .boxWrap .slide .txtBox{
  width: 464px;
  height: 94px;
}
#section1 .boxWrap .slide .txtBox p:nth-child(1){
  height: 30px;
  font-size: 20px;
  font-weight: 700;
}
#section1 .boxWrap .slide .txtBox p:nth-child(2){
  height: 40px;
  font-size: 16px;
  color: #888;
}
#section1 .boxWrap .slide .txtBox p:nth-child(3){
  height: 34px;
}
#section1 .swiper-button-prev:after,
#section1 .swiper-button-next:after{
    font-size: 30px;
    font-weight: 900;
    color: #333;
}
#section1 .swiper-button-next,
#section1 .swiper-button-prev{
    margin-top: -70px;
}
/* section2 */
#section2{
  height: 540px;
  /* background: rgba(255,255,0,0.2); */
  margin-top: 120px;
  position: relative;
}
#section2  .txtBox{
  position: absolute;
  width: 226px; height: 174px;
  z-index: 10;
  left: 170px;
  bottom: 80px;
  text-align: left;
}
#section2  .txtBox h2{
  font-size: 32px; width: 226px; height: 48px;
}
#section2  .txtBox p{
  width: 226px; height: 61px; color: #666;
  font-size: 18px; padding-top: 7px;
  letter-spacing: -2px;
  line-height: 26px;
}
#section2  .txtBox span{
  display: block;
  width: 114px;
  height: 39px;
  color: #222;
  padding: 8px 23px;
  border: 1px solid #222;
  font-size: 14px;
  letter-spacing: -2px;
  margin-top: 26px;
}
/* section3 */
#section3{
  height: 651px;
  margin-top: 120px;
}
#section3 h2{
  width: 1440px; height: 62px; font-size: 28px;
  text-align: left;
  padding-bottom: 20px;
}
#section3 .boxWrap{
  height: 589px;  
}
#section3 .box{
  width: 1440px;
  height: 522px;
  overflow: hidden;
}
#section3 .box ul{
  width: 1440px; display: flex;
  justify-content: space-between;
}
#section3 .box ul li:nth-child(1),
#section3 .box ul li:nth-child(3){
  width: 342px;
  height: 522px;
  position: relative;
  background: #ededed;
  position: relative;
}
#section3 .box ul li:nth-child(2){
  width: 708px;
  height: 522px;
  position: relative;
}
#section3 .box ul li:nth-child(1) .imgBox,
#section3 .box ul li:nth-child(3) .imgBox{
  width: 342px; height: 342px; padding: 12px;
  position: absolute; left: 0; top: 0; 
}
#section3 .box ul li:nth-child(2) .imgBox{
  position: absolute;
  width: 708px;
  height: 522px; left: 0; top: 0;
}
#section3 .box ul li:nth-child(2) .imgBox img{
  height: 100%;
}
#section3 .box ul li:nth-child(1) .txtBox,
#section3 .box ul li:nth-child(3) .txtBox{
  position: absolute; width: 342px; height: 96px;  left: 0; bottom: 50px;
}
#section3 .box ul li:nth-child(2) .txtBox{
  position: absolute; width: 708px; height: 118px; left: 0; bottom: 0;
}
#section3 .box ul li:nth-child(1) .txtBox p:nth-child(1),
#section3 .box ul li:nth-child(3) .txtBox p:nth-child(1){
  font-size: 20px; font-weight: 700; padding-bottom: 3px; width: 342px; height: 33px;
}
#section3 .box ul li:nth-child(1) .txtBox p:nth-child(2),
#section3 .box ul li:nth-child(3) .txtBox p:nth-child(2){
  font-size: 16px; padding-bottom: 15px;
  color: #888; width: 342px; height: 39px;
}
#section3 .box ul li:nth-child(1) .txtBox p:nth-child(3),
#section3 .box ul li:nth-child(3) .txtBox p:nth-child(3){
  width: 342px; height: 24px; font-weight: 500; font-size: 16px;
}
#section3 .box ul li:nth-child(2) .txtBox p:nth-child(1){
  width: 708px; height: 39px; font-size: 24px;
  font-weight: 700; padding: 0 0 3px 62px; text-align: left;
}
#section3 .box ul li:nth-child(2) .txtBox p:nth-child(2){
  width: 708px; height: 79px; padding: 7px 0 48px 62px;
  color: #666; font-size: 16px; text-align: left;
}
/* section4 */
#section4{
  width: 1440px;
  height: 718px;
  /* background: rgba(255,255,0,0.2); */
  margin-top: 120px;
  display: flex;
  justify-content: space-between;
}
#section4 .list{
  width: 707px;
  height: 718px;
  color: #222;
}
#section4 .imgBox{
  height: 599px;
}
#section4 .txtBox{
  height: 119px;
}
#section4 .txtBox h3{
  width: 707px;
  height: 78px;
  font-size: 28px;
  padding: 22px 0 14px;
}
#section4 .txtBox span{
  display: inline-block;
  width: 134px;
  height: 41px;
  font-size: 14px;
  padding: 9px 33px;
  border: 1px solid #222;
  letter-spacing: -2px;
  cursor: pointer;
}
/* footer */
#footer{
  height: 156px;
  /* background: rgba(255,0,0,0.2); */
  margin-top: 80px;
  padding-bottom: 37px;
  font-size: 12px;
  color: #767676;
  border-top: 1px solid #ddd;
}
#footer>.footIn{
  width: 1200px;
  padding: 0 50px;
  height: 55px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fmenu{
  width: 820px;
  height: 55px;
}
.fmenu ul{
  width: 820px;
  height: 55px;
  display: flex;
  align-items: center;
}
.fmenu li a{
  display: block;
  padding: 3px 14px 3px 0;
  border-right: 1px solid #aaa;
  margin-left: 14px;
  font-size: 15px;
}
.fmenu li a:hover{
  text-decoration: underline;
}
.fmenu li:nth-child(4) a{
  font-weight: 900;
}
.fmenu li:last-child a{
  border-right: 0;
}
.fsns{
  width: 240px;
  height: 55px;
}
.fsns ul{
  width: 240px;
  height: 55px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fsns li{
  width: 30px;
  height: 30px;
}
.fsns li:first-child{
  width: 42px;
  height: 28px;
}
.fdown{
  height: 63px;
  border-top: 1px solid #ddd;
}
.fdown .footIn{
  display: block;
  text-align: left;
  width: 1200px;
  padding: 0 65px;
  margin: 0 auto;
}
.fdown .tel{
  width: 820px;
  height: 43px;
  padding-top: 7px;
  font-size: 15px;
}
.copy{
  font-size: 13px;
  height: 20px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
